.home-page {
    background: linear-gradient(135deg, #2b2d42 0%, #4b5e8a 100%);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

.home-container {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
    gap: 2rem;
}

.section-title {
    color: #ffffff;
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 1.5rem;
    text-align: center;
}

